.root {
	li {
		display: inline-flex;
	}

	a {
		display: inline-flex;
		color: #fe5f1e;

		justify-content: center;
		align-items: center;
		width: 45px;
		height: 45px;
		padding: 10px;
		border: 1px solid #fe5f1e;
		border-radius: 30px;
		margin-right: 10px;
		cursor: pointer;
		transition: all 0.2s ease 0s;

		&:hover {
			background-color: #fe5f1e;
			color: #fff;
		}
	}

	:global {
		.selected {
			a {
				background-color: #fe5f1e;
				color: #fff;
			}
		}
	}
}
